	module.exports = {"data":{"markdownRemark":{"html":"<h2>Sprint Planning</h2>\n<p>In Scrum, the sprint planning meeting is attended by the product owner, ScrumMaster and the entire Scrum team. Outside stakeholders may attend by invitation of the team, although this is rare in most companies.\nDuring the sprint planning meeting, the product owner describes the highest priority features to the team. The team asks enough questions that they can turn a high-level user story of the product backlog into the more detailed tasks of the sprint backlog.</p>\n<p>There are two defined artifacts that result from a sprint planning meeting:</p>\n<ul>\n<li>A sprint goal</li>\n<li>A sprint backlog</li>\n</ul>\n<h4>More Information:</h4>\n<p><a href=\"https://www.mountaingoatsoftware.com/agile/scrum/meetings/sprint-planning-meeting\">https://www.mountaingoatsoftware.com/agile/scrum/meetings/sprint-planning-meeting</a></p>","fields":{"slug":"/agile/sprint-planning/"},"frontmatter":{"title":"Sprint Planning"}}},"pathContext":{"slug":"/agile/sprint-planning/"}}

